      "description": "Renton Thurston is a 14-year-old boy who lived with his mechanic grandfather in a backwater town. Every day he dreamed of being with the mercenary \"Light Finding Operation (LFO)\" aircraft pilot group \"Gekkostate\" and riding \"Trapar\" particle waves \u2014a sport called \"reffing\"\u2014 with their charismatic leader Holland, especially when faced with his father's acclaimed past or his grandfather's desire to protect him. When a young girl named Eureka riding the original LFO, the \"Nirvash typeZERO\", asks his grandfather for a tune-up, she inadvertently brings the attention of the military to the garage; as a result, the garage is destroyed and Renton is forced to deliver a new type of interface \u2014the \"Amita Drive\"\u2014 to the Nirvash. After a heated fight in which the Nirvash destroys the military LFOs by unleashing an immense amount of power, Renton is invited into Gekkostate. However, he quickly realizes that behind the facade of a traveling group of mercenaries is a very bitter reality. <br><br>\n(Source: Anime News Network) ",
