      "description": "With the rapid advance of science and technology, artificial intelligence has gained sentience and become godlike beings. The capitalists on the top of the pyramid are not willing to yield to these \"\"Gods\"\", so they conducted human experiments and embedded copy of the \"\"Gods\"\" in humans and obtained powers that could rival the \"\"Gods\"\". The main character \"\"Feng Mo\"\" is the only survivor of these experiments. This story is about the symbiotic relationship between human and artificial intelligence, and how they changed their fate in this world of despair.\n<br><br>\n(Source: Youtube Description)",
