      "description": "The story takes place at a horse racing academy that trains boys to become jockeys. The three-year academy is very competitive, and those who wish to enter must not only pass an academic test but a physical and fitness test as well. Yuu Arimura is a former popular idol who becomes enamored with horse racing after seeing it for the first time, and wants to join the academy. Shun Kanami was raised on an island, and only experienced horse races through radio broadcasts growing up. Amane comes from a high-class family in England, and his father is a former jockey. Amane has previously attended horse racing academies in various countries. The show follows these three 15-year-old boys at the academy.<br>\n<br>\n(Source: Anime News Network)",
