      "description": "High school student Katagiri Yuuichi, who values friendship above all else, enjoys a fulfilling life with his close friends Sawagiri Shiho, Mikasa Tenji, Shibe Makoto, and Kokorogi Yutori.<br>\n<br>\nHowever, after a particular incident, they're dragged into a debt repayment game.<br>\n <br>\nThe only way to beat the \"Tomodachi Game\" is to not doubt their friends. Bound together by solid friendships, the game should've been easy, but\u2013 The hugely popular comic that sold over two million copies is finally becoming an anime! Will they trust or betray their precious friends? The true nature of humanity is exposed in the ultimate psychological game!<br>\n<br>\n(Source: Crunchyroll)<br>\n<br>\n<i>Note: Premieres on Hulu and d Anime Store at 0:00 on Wednesdays, 1 hour 29 minutes ahead of Nippon TV's broadcast.</i>",
