      "description": "The distant future. After reaching its peak, Earth's population fell into decline. AI was developed to help preserve the species and manage the ecosystem; a grand experiment in human diversity. Through genetic engineering, a diverse array of peoples, including beastfolk, magical beings, and more, was created to live in a diverse city separated into walled \"clusters\".<br>\n<br>\nEach cluster developed a unique culture, befitting of the people that lived there. Their lives, and the system, is continually maintained by the AI to ensure survival.<br>\n<br>\n(Source: Polygon Pictures)<br><br>\n<i>Note: The episodes were streamed two weeks ahead of the Japanese broadcast on Crunchyroll beginning on March 23, 2022. The regular TV broadcast started on April 7, 2022.</i>",
